
.nav {
  float: right;
  margin-right: 3.5%;
  margin-top: 4px;
}
.nav ul.list li.listLi {
  float: left;
  color: #fff;
  margin-left: 80px;
  cursor: pointer;
  position: relative;
  height: 30px;
  line-height: 30px;
}
.nav ul.list li.listLi img {
  position: relative;
  margin-left: 8px;
  top: -2px;
  width: 8px;
}
.nav ul.list li.listLi a {
  color: #fff;
  cursor: pointer;
}
.nav ul.list li.listLi ul.listChild {
  position: absolute;
  top: 30px;
  left: -14px;
  display: none;
  background: #fff;
  border-radius: 2px;
  z-index: 9999;
}
.nav ul.list li.listLi ul.listChild li {
  width: 124px;
  height: 42px;
  line-height: 42px;
  font-size: 14px;
  text-align: left;
  padding-left: 16px;
}
.nav ul.list li.listLi ul.listChild li a {
  color: #7A8899;
  display: block;
  width: 100%;
  height: 100%;
}
.nav ul.list li.listLi ul.listChild li a:hover {
  color: #1D6EF0;
}
.nav ul.list li.listLi ul.active {
  display: block;
}
@media only screen and (max-width: 600px) {
.nav ul.list {
    display: none;
}
.nav ul.list li.listLi {
    margin-left: 20px;
}
.nav .menu {
    display: block;
}
}



