
.nav {
  float: right;
  margin-right: 3.5%;
  margin-top: 4px;
}
.nav .menu img {
  display: block;
  width: 24px;
}
.nav .mNav {
  -webkit-transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  padding-top: 10px;
  position: fixed;
  top: 0px;
  left: -210px;
  width: 200px;
  background: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  height: 100%;
  z-index: 9999;
}
.nav .mNav li.listLi {
  color: #7A8899;
  padding-left: 22px;
  font-size: 14px;
  line-height: 40px;
}
.nav .mNav li.listLi .listChild {
  padding-left: 20px;
}
.nav .mNav li.listLi .listChild li a {
  color: #7A8899;
}
.nav .mNavBlock {
  -webkit-transform: translateX(210px);
  transform: translateX(210px);
}
.nav .layer {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background: rgba(0, 0, 0, 0);
  z-index: 9991;
}



