
.bottom {
  width: 100%;
  min-height: 35%;
  background-color: #3B3E4D;
}
.bottom .cml-logo {
  width: 40%;
  float: left;
  margin-top: 60px;
  margin-left: 7%;
  padding-right: 4%;
  padding-bottom: 60px;
}
.bottom .cml-logo img {
  width: 24%;
}
.bottom .cml-logo .cml-logo-text {
  font-size: 12px;
  color: #7A8899;
  line-height: 16px;
  margin-top: 4px;
}
.bottom .row {
  float: right;
  padding-top: 60px;
  width: 30%;
  min-width: 192px;
}
.bottom .row img {
  display: block;
  width: 40%;
  height: 40%;
  margin: 0px auto;
}
.bottom .row p {
  color: #7A8899;
  font-size: 12px;
  text-align: center;
  padding-top: 10px;
}
.bottom .row .link-column {
  float: left;
  margin-right: 10%;
}
.bottom .row .link-column .link-column-title {
  font-size: 12px;
  color: #7A8899;
  padding-bottom: 10px;
}
.bottom .row .link-column a {
  display: block;
  text-decoration: none;
  font-size: 14px;
  color: #AFB7C2;
  margin-top: 10px;
  line-height: 20px;
}
.bottom .row .link-column ul {
  margin-top: 10px;
}
.bottom .row .link-column ul li {
  float: left;
  margin-right: 24px;
  min-height: 50px;
}
.bottom .row .link-column ul li:last-child {
  margin-right: 0px;
}
.bottom .row .link-column:last-child {
  margin-right: 0px;
}
@media only screen and (max-width: 600px) {
.bottom .cml-logo {
    width: 100%;
    float: none;
    border-right: 0px;
    margin: 0px auto;
    padding-top: 20px;
    padding-bottom: 30px;
    padding-left: 20px;
}
.bottom .cml-logo img {
    width: 24%;
    display: block;
}
.bottom .row {
    float: none;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-top: 40px;
}
.bottom .row img {
    width: 100%;
}
.bottom .row .link-column ul {
    max-width: 120px;
    margin-top: 10px;
}
}



